form {
  display: flex;
  justify-content: flex-end;
  margin: 20px 0 20px 0;
}

input[type="radio"] {
  position: absolute;
  visibility: hidden;
  display: none;
}

label {
  color: #fff;
  display: inline-block;
  font-size: 11px;
  font-weight: 500;
  line-height: 13px;
  padding: 10px 10px;
  width: 50%;
}

input[type="radio"]:checked + label {
  color: rgb(30, 30, 30);
  background: rgb(187, 215, 236);
}

.radio_group {
  display: inline-block;
  border-radius: 15px;
  overflow: hidden;
  width: 163px;
  height: 30px;
  background: rgb(30, 30, 30);
}

.current_container {
  height: auto;
  width: 41vw;
  overflow: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
  cursor: pointer;
  color: #fff;
}

@media screen and (max-width: 992px) {
  .current_container {
    width: 100%;
  }
}